* {
    transition: all .3s; 
    box-sizing: border-box;
}

h1,p{
    margin: 0 !important;
    padding: 0 !important;
}

html {
    font-size: 62.5%; //1rem = 10px


}

body {
    background-image:url("../../img/icons\ _\ graphics/Asset 1.png") ,linear-gradient($color-black 85%,$color-black 20% ) ;
    font-family: $fontPopipns;
    background-repeat: repeat-y, repeat-y;
    background-position: left,right;


}


//Scroll bar
::-webkit-scrollbar {
    width: .3rem;
    @include respond(phone){
        display: none;
    }
}

::-webkit-scrollbar-track {
    background:#18191a;
    ;

    @include respond(phone){
        display: none;
    }
}

::-webkit-scrollbar-thumb {
    background: $color-bg-green;
    @include respond(phone){
        display: none;
    }
}

::-webkit-scrollbar-thumb:hover {
    background: $color-bg-green;
    @include respond(phone){
        display: none;
    }

}